	module.exports = {"data":{"markdownRemark":{"html":"<p>This episode, I sit down with our “newbiest” ID yet, Ellen Hayes, and we talk about her process building an online course for a student maker space. Ellen shares her interesting journey towards ID, and how she combined code and software development processes (namely Agile) to go out and study her users and the space, iterate, and build a solution to an understaffing situation.</p>\n<iframe src=\"https://simplecast.com/e/59989?style=medium-light\" width=\"100%\" height=\"94px\" frameborder=\"0\" scrolling=\"no\" seamless=\"\"></iframe>\n<p>Update: Ellen just got a new job! Be sure to congratulate her! Links to more about Ellen’s project on the show page: <a href=\"https://dearinstructionaldesigner.simplecast.fm/\">https://dearinstructionaldesigner.simplecast.fm/</a></p>","frontmatter":{"date":"April 20, 2017","path":"/blog/dearid-episode-42-online-course-for-a-student-maker-space-with-ellen-hayes","title":"#DearID: Episode 42: Online Course for a Student Maker Space with Ellen Hayes","tags":["dear id","elearning","makerspace","newbie","video"],"categories":["Dear ID Show","Design"],"featured":{"childImageSharp":{"sizes":{"base64":"data:image/jpeg;base64,","aspectRatio":3,"src":"/static/twitter-8f833ed6b7e5464c0e3be8d40e769b35-c6512.jpg","srcSet":"/static/twitter-8f833ed6b7e5464c0e3be8d40e769b35-c7d05.jpg 203w,\n/static/twitter-8f833ed6b7e5464c0e3be8d40e769b35-784f5.jpg 405w,\n/static/twitter-8f833ed6b7e5464c0e3be8d40e769b35-c6512.jpg 810w,\n/static/twitter-8f833ed6b7e5464c0e3be8d40e769b35-adada.jpg 1215w,\n/static/twitter-8f833ed6b7e5464c0e3be8d40e769b35-1c372.jpg 1500w","sizes":"(max-width: 810px) 100vw, 810px"}}}}}},"pathContext":{}}

